import React from 'react'
import OwlCarousel from 'react-owl-carousel';
import 'owl.carousel/dist/assets/owl.carousel.css';
import 'owl.carousel/dist/assets/owl.theme.default.css'
import { Link } from 'react-router-dom';

export default function Latest({ title, data }) {
    const option = {
        responsive: {
            0: {
                items: 1.5
            },
            768: {
                items: 6
            }
        },

        dots: false,
        loop: true,
        autoplay: true,
        autoplayTimeout: 3000,
        autoplayHoverPause: true,
        smartSpeed: 900,
        fluidSpeed: true,
        nav: true,
        navText: ['<i class="fa fa-arrow-left text-info px-3 py-1 border-1 border-dark rounded"></i>', '<i class="fa fa-arrow-right text-info px-3 py-1"></i>']
    }
    return (
        <>
            <div className="container-fluid mt-1">
                <div className="testimonial-item img-border-radius shadow-sm bg-white p-3">
                    <h3 className='mb-3'>{title}</h3>
                    <div className="testimonial-carousel">
                        <OwlCarousel className='owl-theme' {...option}>
                            {
                                data.map((item, index) => {
                                    return <div key={index} className="me-2 position-relative testimonial-item">
                                        <Link to={`/product/${item._id}`} >
                                            <div className="rounded position-relative border border-dark">
                                                <div className="vesitable-img">
                                                    <img src={item.pic[0]} style={{ height: 200 }} className="img-fluid w-100 rounded-top" alt="" />
                                                </div>
                                                <div className="text-white bg-dark px-2 rounded position-absolute" style={{ top: "10px", left: "10px", fontSize: "15px" }}>{item.brand.name}</div>
                                                <div className="py-3">
                                                    <Link to={`/product/${item._id}`}><h6 className='px-2 truncate'>{item.name.slice(0, 15) + "..."}</h6></Link>
                                                    <div className="d-flex flex-lg-wrap px-2 gap-2">
                                                        <p className='extra'>&#8377;<del className='text-danger'>{item.basePrice}</del></p>
                                                        <p className='extra'>&#8377;{item.finalPrice} <sup>{item.discount}% off</sup></p>
                                                    </div>

                                                </div>
                                            </div>
                                        </Link>
                                    </div>
                                })
                            }
                        </OwlCarousel>
                    </div>
                </div>
            </div>
        </>
    )
}

