import React from 'react'
import { Link } from 'react-router-dom'
import '../../assets/custom.css'

export default function Category({ title, data }) {

    return (
        <>
            <div className="container-fluid fruite mt-3">
                <div className="testimonial-item img-border-radius shadow-sm bg-white py-3 px-2">
                    <div className="tab-class">
                        <div className="row g-4">
                            <div className="col-lg-4 text-start mb-2">
                                <h3>{title}</h3>
                            </div>
                            <div className="col-lg-8 text-end d-none d-lg-block">
                                <ul className="nav nav-pills d-inline-flex text-center mb-3">
                                    <li className="nav-item">
                                        <a className="d-flex m-2 py-1 px-3 bg-light rounded-pill active" data-bs-toggle="pill" href="#tab-1">
                                            <span className="text-dark " style={{ fontSize: "14px" }}>All Products</span>
                                        </a>
                                    </li>
                                    <li className="nav-item">
                                        <a className="d-flex py-1 m-2 px-3 bg-light rounded-pill" data-bs-toggle="pill" href="#tab-2">
                                            <span className="text-dark " style={{ fontSize: "14px" }}>Men's</span>
                                        </a>
                                    </li>
                                    <li className="nav-item">
                                        <a className="d-flex m-2 py-1 px-3 bg-light rounded-pill" data-bs-toggle="pill" href="#tab-3">
                                            <span className="text-dark " style={{ fontSize: "14px" }}>Women's</span>
                                        </a>
                                    </li>
                                    <li className="nav-item">
                                        <a className="d-flex m-2 py-1 px-3 bg-light rounded-pill" data-bs-toggle="pill" href="#tab-4">
                                            <span className="text-dark " style={{ fontSize: "14px" }}>Kid's</span>
                                        </a>
                                    </li>
                                    <li className="nav-item">
                                        <a className="d-flex m-2 py-1 px-3 bg-light rounded-pill" data-bs-toggle="pill" href="#tab-5">
                                            <span className="text-dark " style={{ fontSize: "14px" }}>Electronics</span>
                                        </a>
                                    </li>
                                </ul>
                            </div>
                        </div>
                        <div className="tab-content">
                            <div id="tab-1" className="tab-pane fade show p-0 active">
                                <div className="row">
                                    <div className="col-lg-12">
                                        <div className="row gx-2 gy-2">
                                            {
                                                data.slice(0, 12).map((item, index) => {
                                                    return <div key={index} className="col-6 col-md-4 col-lg-3 col-xl-2">
                                                        <Link to={`/product/${item._id}`} >
                                                            <div className="rounded position-relative fruite-item  border border-dark">
                                                                <div className="fruite-img">
                                                                    <img src={item.pic[0]} className="img-fluid w-100 rounded-top category-image" alt="" />
                                                                </div>
                                                                <div className="text-white bg-dark px-2 rounded position-absolute" style={{ top: "10px", left: "10px", fontSize: "15px" }}>{item.brand.name}</div>
                                                                <div className="py-2">
                                                                    <Link to={`/product/${item._id}`}><h6 className='px-2'>{item.name.slice(0, 25)}</h6></Link>
                                                                    <div className="d-flex flex-lg-wrap px-2">
                                                                        <span>&#8377;{item.finalPrice} <sup>{item.discount}% off</sup></span>
                                                                    </div>

                                                                </div>
                                                            </div>
                                                        </Link>
                                                    </div>
                                                })

                                            }
                                        </div>
                                    </div>
                                </div>
                            </div>
                            <div id="tab-2" className="tab-pane fade show p-0">
                                <div className="row g-4">
                                    <div className="col-lg-12">
                                        <div className="row g-4">
                                            {
                                                data.some((x) => x.maincategory.name === "Male") ?
                                                    data.filter((x) => x.maincategory.name === "Male").slice(0, 6).map((item, index) => {
                                                        return <div key={index} className="col-md-4 col-lg-3 col-xl-2">
                                                            <Link to={`/product/${item._id}`} >
                                                                <div className="rounded position-relative fruite-item border border-dark">
                                                                    <div className="fruite-img">
                                                                        <img src={item.pic[0]} style={{ height: 200 }} className="img-fluid w-100 rounded-top" alt="" />
                                                                    </div>
                                                                    <div className="text-white bg-dark px-2 rounded position-absolute" style={{ top: "10px", left: "10px", fontSize: "15px" }}>{item.brand.name}</div>
                                                                    <div className="py-3">
                                                                        <Link to={`/product/${item._id}`}><h6 className='px-2'>{item.name.slice(0, 25)}</h6></Link>
                                                                        <div className="d-flex flex-lg-wrap px-2">
                                                                            <p className='extra'>&#8377;<del className='text-danger'>{item.basePrice}</del></p>
                                                                            <p className='extra ms-4'>&#8377;{item.finalPrice} <sup>{item.discount}% off</sup></p>
                                                                        </div>

                                                                    </div>
                                                                </div>
                                                            </Link>
                                                        </div>
                                                    })
                                                    :
                                                    <p>No Male products available.</p>
                                            }
                                        </div>
                                        <Link to="shop/?mc=Male" className='float-end m-2 btn border-1 border-info px-2 p-0' style={{ fontSize: "14px" }}>More</Link >
                                    </div>
                                </div>
                            </div>
                            <div id="tab-3" className="tab-pane fade show p-0">
                                <div className="row g-4">
                                    <div className="col-lg-12">
                                        <div className="row g-4">
                                            {
                                                data.some((x) => x.maincategory.name === "Female") ?
                                                    data.filter((x) => x.maincategory.name === "Female").slice(0, 6).map((item, index) => {
                                                        return <div key={index} className="col-md-4 col-lg-3 col-xl-2">
                                                            <Link to={`/product/${item._id}`} >
                                                                <div className="rounded position-relative fruite-item border border-dark">
                                                                    <div className="fruite-img">
                                                                        <img src={item.pic[0]} style={{ height: 200 }} className="img-fluid w-100 rounded-top" alt="" />
                                                                    </div>
                                                                    <div className="text-white bg-dark px-2 rounded position-absolute" style={{ top: "10px", left: "10px", fontSize: "15px" }}>{item.brand.name}</div>
                                                                    <div className="py-3">
                                                                        <Link to={`/product/${item._id}`}><h6 className='px-2'>{item.name.slice(0, 25)}</h6></Link>
                                                                        <div className="d-flex flex-lg-wrap px-2">
                                                                            <p className='extra'>&#8377;<del className='text-danger'>{item.basePrice}</del></p>
                                                                            <p className='extra ms-4'>&#8377;{item.finalPrice} <sup>{item.discount}% off</sup></p>
                                                                        </div>

                                                                    </div>
                                                                </div>
                                                            </Link>
                                                        </div>
                                                    })
                                                    :
                                                    <p>No Female products available.</p>
                                            }
                                        </div>
                                    </div>
                                </div>
                            </div>
                            <div id="tab-4" className="tab-pane fade show p-0">
                                <div className="row g-4">
                                    <div className="col-lg-12">
                                        <div className="row g-4">
                                            {
                                                data.some((x) => x.maincategory.name === "Kids") ?
                                                    data.filter((x) => x.maincategory.name === "Kids").slice(0, 6).map((item, index) => {
                                                        return <div key={index} className="col-md-4 col-lg-3 col-xl-2">
                                                            <Link to={`/product/${item._id}`} >
                                                                <div className="rounded position-relative fruite-item border border-dark">
                                                                    <div className="fruite-img">
                                                                        <img src={item.pic[0]} style={{ height: 200 }} className="img-fluid w-100 rounded-top" alt="" />
                                                                    </div>
                                                                    <div className="text-white bg-dark px-2 rounded position-absolute" style={{ top: "10px", left: "10px", fontSize: "15px" }}>{item.brand.name}</div>
                                                                    <div className="py-3">
                                                                        <Link to={`/product/${item._id}`}><h6 className='px-2'>{item.name.slice(0, 25)}</h6></Link>
                                                                        <div className="d-flex flex-lg-wrap px-2">
                                                                            <p className='extra'>&#8377;<del className='text-danger'>{item.basePrice}</del></p>
                                                                            <p className='extra ms-4'>&#8377;{item.finalPrice} <sup>{item.discount}% off</sup></p>
                                                                        </div>

                                                                    </div>
                                                                </div>
                                                            </Link>
                                                        </div>
                                                    })
                                                    :
                                                    <p>No Kid's products available.</p>
                                            }
                                        </div>
                                    </div>
                                </div>
                            </div>
                            <div id="tab-5" className="tab-pane fade show p-0">
                                <div className="row g-4">
                                    <div className="col-lg-12">
                                        <div className="row g-4">
                                            {
                                                data.some((x) => x.maincategory.name === "Electronics") ?
                                                    data.filter((x) => x.maincategory.name === "Electronics").slice(0, 6).map((item, index) => {
                                                        return <div key={index} className="col-md-4 col-lg-3 col-xl-2">
                                                            <Link to={`/product/${item._id}`} >
                                                                <div className="rounded position-relative fruite-item border border-dark">
                                                                    <div className="fruite-img">
                                                                        <img src={item.pic[0]} style={{ height: 200 }} className="img-fluid w-100 rounded-top" alt="" />
                                                                    </div>
                                                                    <div className="text-white bg-dark px-2 rounded position-absolute" style={{ top: "10px", left: "10px", fontSize: "15px" }}>{item.brand.name}</div>
                                                                    <div className="py-3">
                                                                        <Link to={`/product/${item._id}`}><h6 className='px-2'>{item.name.slice(0, 25)}</h6></Link>
                                                                        <div className="d-flex flex-lg-wrap px-2">
                                                                            <p className='extra'>&#8377;<del className='text-danger'>{item.basePrice}</del></p>
                                                                            <p className='extra ms-4'>&#8377;{item.finalPrice} <sup>{item.discount}% off</sup></p>
                                                                        </div>

                                                                    </div>
                                                                </div>
                                                            </Link>
                                                        </div>
                                                    })
                                                    :
                                                    <p>No electronics products available.</p>
                                            }
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </>
    )
}
