import React from 'react'
import { Link } from 'react-router-dom'

export default function Bestsellar({ title, data }) {
    return (
        <>
            <div className="container-fluid mt-3">
                <div className="testimonial-item img-border-radius shadow-sm bg-white p-3">
                    <div className="text-center mx-auto mb-2" style={{ maxWidth: " 700px" }}>
                        <h3>{title}</h3>
                        <p>Explore our best-selling products, chosen for their quality, performance, and value.</p>
                    </div>
                    <div className="row g-2">
                        {
                            data.slice(0, 6).map((item, index) => {
                                return <div key={index} className="col-12 col-md-6 col-lg-4 mb-2">
                                    <div className="p-2 rounded bg-light">
                                        <div className="row align-items-center">
                                            <div className="col-4 col-md-6">
                                                <img
                                                    src={item.pic[0]}
                                                    className="img-fluid rounded-circle w-100"
                                                    alt=""
                                                    style={{ height: 'auto', maxHeight: '190px' }}
                                                />
                                            </div>
                                            <div className="col-8 col-md-6">
                                                <Link href="#" className="h5 text-truncate product-name">{item.name.slice(0, 19)}</Link>
                                                <div className="d-flex my-2 rating-star">
                                                    <i className="fas fa-star text-info"></i>
                                                    <i className="fas fa-star text-info"></i>
                                                    <i className="fas fa-star text-info"></i>
                                                    <i className="fas fa-star text-info"></i>
                                                    <i className="fas fa-star"></i>
                                                </div>
                                                <div className='d-flex gap-2 best-price mb-2'>
                                                    <p className='extra'>&#8377;<del className='text-danger'>{item.basePrice}</del></p>
                                                    <p className='extra'>&#8377;{item.finalPrice} <sup>{item.discount}% off</sup></p>
                                                </div>
                                                <span><Link to={`/product/${item._id}`} className="related px-3 py-2 rounded-pill bg-info"><i className="fa fa-shopping-bag me-2"></i> Add to cart</Link></span>
                                            </div>
                                        </div>
                                    </div>
                                </div>

                            })
                        }
                    </div>
                </div>
            </div>
        </>
    )
}
