import React, { useEffect, useState } from 'react'
import { useDispatch, useSelector } from 'react-redux'
import { getProduct } from '../../Redux/ActionCreator/ProductActionCreator'
import { Link } from 'react-router-dom'
import '../../assets/custom.css'

export default function Feature() {
    let [product, setProducts] = useState()
    let dispatch = useDispatch();
    let ProductStateData = useSelector(state => state.ProductStateData)

    useEffect(() => {
        (() => {
            dispatch(getProduct())
            if (ProductStateData.length)
                setProducts(ProductStateData)
        })()
    }, [ProductStateData.length])

    return (
        <div className='container-fluid mt-3'>
            <div className='row gx-2 '>
                {
                    product?.slice(0, 4).map((item, index) => {
                        return <div key={index} className="col-6 col-md-4 col-lg-3 mb-2">
                            <div className="img-border-radius shadow-sm bg-white px-3 py-3">
                                <p className="small-text">{item?.name.slice(0, 12) + "..."}</p>
                                <div className="d-flex justify-content-center">
                                    <img src={`/${item?.pic[0]}`} className="img-fluid small-image" alt="product" />
                                </div>
                                <Link to={`/shop/?sc=${item?.subcategory?.name}`} className="text-info" style={{ fontSize: "12px" }}>Explore More</Link>
                            </div>
                        </div>
                    })
                }
            </div>
        </div>
    )
}
