import React, { useEffect, useState } from 'react'
import Testimonial from './Partial/Testimonial'
import Fact from './Partial/Fact'
import Bestsellar from './Partial/Bestsellar'
import Latest from './Partial/Latest'
import Category from './Partial/Category'
import { Link } from 'react-router-dom'
import { getProduct } from '../Redux/ActionCreator/ProductActionCreator'
import { useDispatch, useSelector } from 'react-redux'
import Brandslider from './Partial/Brandslider'
import '../assets/custom.css'
import Feature from './Partial/Feature'
import Banner from './Partial/Banner'


export default function Home() {
    let [products, setProducts] = useState([])

    let dispatch = useDispatch();
    let ProductStateData = useSelector(state => state.ProductStateData)

    useEffect(() => {
        (() => {
            dispatch(getProduct())
            if (ProductStateData.length)
                setProducts(ProductStateData)
        })()
    }, [ProductStateData.length])



    return (
        <>
            <div className="container-fluid mt-2">
                <div className="testimonial-item img-border-radius shadow-sm bg-white">
                    <div className="container">
                        <div className="d-flex gap-3 gap-md-4 gap-lg-5 py-3 top-scroll justify-content-md-center">
                            {[
                                { link: "shop/?mc=Fashion", img: "./img/fashion.jpg", label: "Fashion" },
                                { link: "shop/?mc=Mobile", img: "./img/mobile.jpg", label: "Mobile" },
                                { link: "shop/?mc=Grocery", img: "./img/grocery.jpg", label: "Grocery" },
                                { link: "shop/?mc=Furnitures", img: "./img/furniture.jpg", label: "Furnitures" },
                                { link: "shop/?mc=Appliance", img: "./img/appliance.jpg", label: "Appliance" },
                                { link: "shop/?mc=Electronics", img: "./img/hardware.jpg", label: "Electronics" },
                                { link: "shop/?mc=Cosmetics", img: "./img/cosmetics.jpg", label: "Cosmetics" },
                                { link: "shop/?mc=Toys", img: "./img/toy.jpg", label: "Toys" },
                                { link: "shop/?mc=Sports", img: "./img/sport.jpg", label: "Sports" },
                            ].map((item, index) => (
                                <Link key={index} to={item.link} className="text-center">
                                    <img
                                        src={item.img}
                                        className="rounded-circle"
                                        style={{ height: "60px", width: "60px", maxWidth: "80px" }}
                                        alt=""
                                    />
                                    <p className="extra pt-2 text-dark" style={{ fontSize: "0.9rem" }}>
                                        <strong>{item.label}</strong>
                                    </p>
                                </Link>
                            ))}
                        </div>
                    </div>
                </div>
                <div className="testimonial-item img-border-radius shadow-sm bg-white mt-3">
                    <div id="carouselId" className="carousel slide position-relative carousel-container" data-bs-ride="carousel">
                        <div className="carousel-inner">
                            {["img/img3.jpg", "img/img4.jpg", "img/img3.jpg", "img/img4.jpg", "img/img3.jpg"].map((src, index) => (
                                <div key={index} className={`carousel-item${index === 0 ? " active" : ""}`}>
                                    <img
                                        src={src}
                                        className="img-fluid carosel-img"
                                        alt={`Slide ${index + 1}`}
                                    />
                                </div>
                            ))}
                        </div>
                        <button
                            className="carousel-control-prev"
                            type="button"
                            data-bs-target="#carouselId"
                            data-bs-slide="prev"
                        >
                            <span className="carousel-control-prev-icon" aria-hidden="true"></span>
                            <span className="visually-hidden">Previous</span>
                        </button>
                        <button
                            className="carousel-control-next"
                            type="button"
                            data-bs-target="#carouselId"
                            data-bs-slide="next"
                        >
                            <span className="carousel-control-next-icon" aria-hidden="true"></span>
                            <span className="visually-hidden">Next</span>
                        </button>
                    </div>
                </div>
            </div>
            <Category title="Products Category" data={products} />
            <Feature />
            <Latest title="Latest Products" data={products.slice(0, 10)} />
            <Banner />
            <Bestsellar title="BestSeller Products" data={products.slice(0, 9)} />
            <Brandslider />
            <Fact />
            <Testimonial />
        </>
    )
}

